import React, { useState, Fragment } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { addExperience } from '../../actions/profile';

const AddExperience = ({ addExperience, history }) => {
	const [ experienceData, setExperienceData ] = useState({
		company: '',
		title: '',
		location: '',
		from: '',
		to: '',
		current: false,
		description: ''
	});
	const { company, title, location, from, to, current, description } = experienceData;

	const [ toDate, toggleToDate ] = useState(true);

	const onChange = (e) => {
		setExperienceData({ ...experienceData, [e.target.name]: e.target.value });
	};

	const onSubmit = (e) => {
		e.preventDefault();
		addExperience(experienceData, history);
	};

	return (
		<Fragment>
			<h1 className='large text-primary'>Add an experience</h1>
			<p className='lead'>
				<i className='fas fa-code-branch' /> Add any developer/programming positions that you have had in the
				past
			</p>
			<small>* = required field</small>
			<form className='form' onSubmit={onSubmit}>
				<div className='form-group'>
					<input type='text' placeholder='* Job Title' name='title' value={title} onChange={onChange} />
				</div>
				<div className='form-group'>
					<input type='text' placeholder='* Company' name='company' value={company} onChange={onChange} />
				</div>
				<div className='form-group'>
					<input type='text' placeholder='Location' name='location' value={location} onChange={onChange} />
				</div>
				<div className='form-group'>
					<h4>From Date</h4>
					<input type='date' name='from' value={from} onChange={onChange} />
				</div>
				<div className='form-group'>
					<p>
						<input
							type='checkbox'
							name='current'
							value={current}
							checked={current}
							onClick={() => {
								setExperienceData({ ...experienceData, current: !current });
								toggleToDate(!toDate);
							}}
						/>{' '}
						Current Job
					</p>
				</div>
				<div className='form-group'>
					<h4>To Date</h4>
					<input type='date' name='to' value={to} onChange={onChange} disabled={!toDate} />
				</div>
				<div className='form-group'>
					<textarea
						name='description'
						cols='30'
						rows='5'
						placeholder='Job Description'
						value={description}
						onChange={onChange}
					/>
				</div>
				<input type='submit' className='btn btn-primary my-1' />
				<Link className='btn btn-light my-1' to='/dashboard'>
					Go Back
				</Link>
			</form>
		</Fragment>
	);
};

AddExperience.propTypes = {
	addExperience: PropTypes.func.isRequired
};

export default connect(null, { addExperience })(withRouter(AddExperience));
